.section-wrapper4 {

    .section {
        background-color: #050913;
        
        .content-container {
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            > p.first {
                    font-size: 13px;
                    line-height: 18px;
                    font-weight: 500;
                    font-style: normal;
                    text-transform: uppercase;
                    letter-spacing: 0.9px;
                    font-family: "Plus Jakarta Sans", sans-serif;
                    color: #FDFDFD;
                    margin: 0;
                    margin-top: -6vw;
                }

            > p.second {
                    font-size: 17px;
                    line-height: 17px;
                    font-weight: 400;
                    font-style: normal;
                    letter-spacing: 0.9px;
                    font-family: "Plus Jakarta Sans", sans-serif;
                    color: #FDFDFD;
                    margin: 0;
                    margin-top: 1vh;


            }

            h2 {
                font-size: 60px;
                color: #FDFDFD;
                font-family: "Plus Jakarta Sans", sans-serif;
                font-weight: 700;
                font-style: normal;
                line-height: 1.086em;
                text-decoration: none;
                text-transform: none;
                letter-spacing: -1.7px;
                line-height: 1.086em;
                margin: 0;
                padding: 20px 0;
                text-align: center;
            }

            .types {
                margin-top: 8vh;
                width: 70%;
                height: 350px;
                border: 0px solid white;
                display: flex;
                justify-content: center;
                gap: 2.5vw;
                


                .type {
                    width: 30%;
                    border: 0px solid white;
                    height: 100%;
                    opacity: 0.9;
                    display: flex;
                    align-items: flex-end;
                    box-sizing: border-box;
                    justify-content: center;
                    cursor: pointer;

                    h5 {
                        color: #FDFDFD;
                        font-family: "Plus Jakarta Sans", sans-serif;
                        font-weight: 700;
                        font-style: normal;
                        line-height: 1.086em;
                        text-decoration: none;
                        text-transform: none;
                        letter-spacing: -1px;

                        font-size: 1.556em;
                        line-height: 1.3em;
                        font-weight: 700;
                        position: relative;
                        bottom: -90px;

                    }
                    
                }
                    .type:nth-child(1) {
                    background-image: url("../../public/media/images/website.png");
                    background-size: cover;

                }
                .type:nth-child(2) {
                    background-image: url("../../public/media/images/application.png");
                    background-size: cover;

                }
                .type:nth-child(3) {
                    background-image: url("../../public/media/images/platform.png");
                    background-size: cover;

                }
             
            }

        }
    }
}