.section-wrapper2 {

    .section {
        background-color: #050913;
        
        .content-container {
            width: 100%;
            height: 100vh;
            padding: 15vw 0;
            display: flex;
            justify-content: center;
            align-items: center;
            box-sizing: border-box;
            border: 0px solid white;
           

            .left, .right {
                border: 0px solid white;
                height: 64vh;
                width: 34vw;
            }

            .left {
                display: flex;
                flex-direction: column;
                justify-content: center;
                padding: 0 30px 35px;
                
                > p {
                    font-size: 13px;
                    line-height: 18px;
                    font-weight: 500;
                    font-style: normal;
                    text-transform: uppercase;
                    letter-spacing: 0.9px;
                    font-family: "Plus Jakarta Sans", sans-serif;
                    color: #FDFDFD;
                    margin: 0;


                }

                h2 {
                    font-size: 54px;
                    color: #FDFDFD;
                    font-family: "Plus Jakarta Sans", sans-serif;
                    font-weight: 700;
                    font-style: normal;
                    line-height: 1.086em;
                    text-decoration: none;
                    text-transform: none;
                    letter-spacing: -1.7px;
                    line-height: 1.086em;
                    margin: 0;
                    padding: 20px 0;
                }
                .list {
                    width: 100%;
                    .item {
                        display: flex;
                        align-items: center;
                        gap: 20px;
                        padding: 10px 0;

                        .line {
                            width: 70px;
                            height: 1px;
                            border-top: 2px solid #2A2E36;

                        }
                        p {
                            font-size: 18px;
                            line-height: 32px;
                            font-weight: 400;
                            letter-spacing: 0;
                            color: #A9A7B0;
                            font-family: "DM Sans", sans-serif;
                            text-align: center;
                            padding: 0;
                            margin: 0;
            
                        }
                    }
                }
            }

            .right {
                position: relative;
                img {
                    width: 35vh;
                    height: 35vh;
                    position: absolute;
                    object-fit: cover;
                }
                img:nth-child(1) {
                    right: 2vw;
                    top: 2vh;
                }
                img:nth-child(2) {
                    left: 4vw;
                    bottom: 0vh;
                }
                .codeLines {
                    width: 250px;
                    height: 171px;
                    box-sizing: border-box;
                    background-color: blue;
                    position: absolute;
                    top: 20vh;
                    left: -3vw;
                    padding: 35px 35px 40px 35px;
                    background-color: #6600FF;
                    box-shadow: 0px 0px 40px 12px rgba(0, 0, 0, 0.05);

                    h1, p {
                        font-family: "Plus Jakarta Sans", sans-serif;
                        line-height: 1.4em;
                        font-weight: 700;
                        font-style: normal;
                        text-transform: none;
                        letter-spacing: 0;
                        color: #FDFDFD;
                        margin: 0;
                    }
                    p {
                        font-size: 19px;
                    }
                    h1 {
                        font-size: 54px;

                    }
                }
            }
        }
    }
}