body {
    padding: 0;
    margin: 0;
    overflow: hidden;
    height: 100vh;
    background-color: #0A0B20;
    [role="main"] {
        height: 100%;
        width: 100%;
        max-width: 100%;
        overflow: auto;
        overflow-x: hidden;

        .section-wrapper {
            width: 100vw;
            position: relative;

            .section {
                position: sticky;
                top: 0;
                width: 100%;
                max-width: 100vw;
                height: 100vh;
                display: flex;
                justify-content: center;
                overflow: hidden;
            }
        }
    }
}





