.section-wrapper3 {

    .section {
        background-color: #050913;
        
        .content-container {
            width: 100%;
            height: 100%;
            border: 0px solid white;
            display: flex;
            align-items: center;
            padding-top: 35px;
            .left, .right {
                width: 50%;
                height: 84%;
                border: 0px solid white;
            }
            .left {
                position: relative;
                .img-container {
                    width: 100%;
                    height: 100%;
                    overflow: hidden;
                    .img {
                        width: 100%;
                        height: 100%;
                        background-image: url(https://ann.axiomthemes.com/wp-content/uploads/2023/03/img_113.jpg);
                        background-position: center center;
                        background-size: cover;
                        animation: scale 15s ease infinite;
                    }
                    
                }
                .codeLines {
                    width: 320px;
                    height: 204px;
                    box-sizing: border-box;
                    background-color: blue;
                    position: absolute;
                   
                    padding: 35px 35px 40px 35px;
                    box-shadow: 0px 0px 40px 12px rgba(0, 0, 0, 0.05);

                    h1, p, span {
                        font-family: "Plus Jakarta Sans", sans-serif;
                        line-height: 1.4em;
                        font-weight: 700;
                        font-style: normal;
                        text-transform: none;
                        letter-spacing: 0;
                        color: #FDFDFD;
                        margin: 0;
                    }
                    p {
                        font-size: 19px;
                    }
                    h1 {
                        font-size: 54px;

                    }
                    span, {
                        font-size: 15px;
                        font-weight: 500;

                    }
                }
                .codeLines.light {
                    top: 55vh;
                    left: 18.5vw;
                    position: absolute;
                    background-color: #6600FF;
                }
                .codeLines.dark {
                    top: 55vh;
                    left: 36.5vw;
                    position: absolute;
                    background-color: #0D111A;
                }
            }
            .right {
                padding: 0px 150px;
                box-sizing: border-box;
                display: flex;
                flex-direction: column;
                justify-content: center;
                > p {
                    font-size: 13px;
                    line-height: 18px;
                    font-weight: 500;
                    font-style: normal;
                    text-transform: uppercase;
                    letter-spacing: 0.9px;
                    font-family: "Plus Jakarta Sans", sans-serif;
                    color: #FDFDFD;
                    margin: 0;


                }

                h2 {
                    font-size: 60px;
                    color: #FDFDFD;
                    font-family: "Plus Jakarta Sans", sans-serif;
                    font-weight: 700;
                    font-style: normal;
                    line-height: 1.086em;
                    text-decoration: none;
                    text-transform: none;
                    letter-spacing: -1.7px;
                    line-height: 1.086em;
                    margin: 0;
                    padding: 20px 0;
                }
                .list {
                    width: 100%;
                    .item {
                        display: flex;
                        align-items: center;
                        gap: 20px;
                        padding: 25px 0;
                        cursor: pointer;


                        p {
                            font-size: 19px;
                            line-height: 32px;
                            font-weight: 700;
                            letter-spacing: 0;
                            color: #FDFDFD;
                            font-family: "DM Sans", sans-serif;
                            text-align: center;
                            padding: 0;
                            margin: 0;
                            display: flex;
                            align-items: center;
                            gap: 10px;

            
                        }
                    }
                    .line {
                        width: 650px;
                        height: 1px;
                        border-top: 1px solid #2A2E36;

                    }
                }
            }
        }
    }
}
@keyframes scale {
    0% {
        scale: 1;
    }
    50% {
        scale: 1.1;  
    }
    100% {
        scale: 1;
    }

}