.section-wrapper7 {

    .section {
        background-color: #0A0B20;
        padding-top: 100px;
        box-sizing: border-box;
       
        .content-container {
            width: 50%;
            height: 100%;
            background-image: url(https://ann.axiomthemes.com/splash/src/img/reward/bg.jpg);
            background-size: cover;
            background-position: center;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: flex-start;

            img {
                width: 70%;
                position: relative;
            }
            h2  {
                text-align: center;
                  font-size: 60px;
                    color: #FDFDFD;
                    font-family: "Plus Jakarta Sans", sans-serif;
                    font-weight: 700;
                    font-style: normal;
                    line-height: 130%;
                    text-decoration: none;
                    text-transform: none;
                    letter-spacing: 0px;
             
                    margin: 0;
            }
        }
    }
}