.section-wrapper1 {
    height: 4000px;

    .section1 {
        background-color: #0A0B20;
        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-image: url("../../public/media/images/bg-section1.png");
            background-size: cover;
            background-position: center top;
            z-index: -1;
        }

        display: flex;
        justify-content: center;
        align-items: center;

        .lines {
            width: 100%;
            height: 100%;
            position: absolute;
            top: -3vh;

            .line-container {
                width: 100%;
                height: 1px;
                background-color: rgba(255, 255, 255, 0.1);
                position: absolute;

                .animated-line {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 150px;
                    height: 100%;
                    background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, white 100%);
                    animation: moveLine 3s linear infinite;
                }

            }
            .line-container:nth-child(1) {
                top: 21%;
                left: 0;
                transform: rotate(90deg);
            }
            .line-container:nth-child(2) {
                top: 21%;
                left: 4.5%;
                transform: rotate(75deg);
            }
            .line-container:nth-child(3) {
                top: 21%;
                right: 4.5%;
                transform: rotate(105deg);
            }
            .line-container:nth-child(4) {
                top: 21%;
                left: 9.5%;
                transform: rotate(60deg);
            }
            .line-container:nth-child(5) {
                top: 21%;
                right: 9.5%;
                transform: rotate(120deg);
            }
            .line-container:nth-child(6) {
                top: 21%;
                left: 15.8%;
                transform: rotate(45deg);
            }
            .line-container:nth-child(7) {
                top: 21%;
                right: 15.8%;
                transform: rotate(135deg);
            }
            .line-container:nth-child(8) {
                top: 21%;
                left: 25%;
                transform: rotate(30deg);
            }
            .line-container:nth-child(9) {
                top: 21%;
                right: 25%;
                transform: rotate(150deg);
            }
            .line-container:nth-child(10) {
                top: 21%;
                left: 48.5%;
                transform: rotate(15deg);
                .animated-line {
                    animation-delay: 0.6s;
                }
            }
            .line-container:nth-child(11) {
                top: 21%;
                right: 48.5%;
                transform: rotate(165deg);
                .animated-line {
                    animation-delay: 0.6s;
                }
            }

        }

        .circle-container {
            position: relative;
            height: 120vh;
            top: -147px;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            width: 120vh;
            gap: 12px;
            overflow: hidden;
            border-radius: 50%;

            .circle {
                width: 100%;
                object-fit: cover;
                position: absolute;
                top: 0;
                left: 0;
            }
            h1 {
                text-align: center;
                z-index: 10;
                color: #FDFDFD;
                font-family: "Plus Jakarta Sans", sans-serif;
                font-weight: 700;
                font-size: 5.565rem;
                letter-spacing: -0.02em;
                line-height: 1.035;
                margin: 0;
                padding: 0;
                margin-top: 200px;
                min-width: 120vh;

                span.highlight {
                    -webkit-background-clip: text;
                    -webkit-text-fill-color: transparent;
                    background-size: 200% 100%;
                    animation: text-bg-animate 5s ease infinite;
                }

                span.premium {
                    background-image: linear-gradient(122deg, #edb89c 0%, #7769e6 100%);
                }
                span.development {
                    background-image: linear-gradient(122deg, #edb89c 0%, #7769e6 100%);
                }

            }
            p {

                max-width: 736px;
                font-size: 19px;
                line-height: 32px;
                font-weight: 400;
                letter-spacing: 0;
                color: #B3B3B3;
                font-family: "DM Sans", sans-serif;
                text-align: center;
                z-index: 2;
                min-width: 57vh;
            }
            .button {
                font-size: 14px;
                margin-bottom: 2.4em;
                background: linear-gradient(230deg, #fa7f3f 0%, #e92f4f 50%, #cb1a77 100%);
                padding: 15px 20px;
                border-radius: 90px;
                font-family: "Plus Jakarta Sans", sans-serif;
                text-transform: uppercase;
                color: #FDFDFD;
                line-height: 1em;
                letter-spacing: 0.1em;
                font-weight: 600;
                z-index: 2;
                cursor: pointer;
            }
        }
        .services {
            .title {
                scale: 0.8;
                margin-top: -79px;
                position: absolute;
                top: -30%;
                left: 30%;
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 50px;

                h1 {
                    text-align: center;
                    z-index: 10;
                    color: #FDFDFD;
                    font-family: "Plus Jakarta Sans", sans-serif;
                    font-weight: 700;
                    font-size: 5.565rem;
                    letter-spacing: -0.02em;
                    line-height: 1.035;
                    margin: 0;
                    padding: 0;
                    
                   
                    span.highlight {
                        -webkit-background-clip: text;
                        -webkit-text-fill-color: transparent;
                        background-size: 200% 100%;
                        animation: text-bg-animate 5s ease infinite;
                    }

                    span.premium {
                        background-image: linear-gradient(122deg, #edb89c 0%, #7769e6 100%);
                    }
                    span.development {
                        background-image: linear-gradient(122deg, #edb89c 0%, #7769e6 100%);
                    }
                } 
                .button {
                    font-size: 14px;
                    margin-bottom: 2.4em;
                    background: linear-gradient(230deg, #fa7f3f 0%, #e92f4f 50%, #cb1a77 100%);
                    padding: 15px 20px;
                    border-radius: 90px;
                    font-family: "Plus Jakarta Sans", sans-serif;
                    text-transform: uppercase;
                    color: #FDFDFD;
                    line-height: 1em;
                    letter-spacing: 0.1em;
                    font-weight: 600;
                    z-index: 2;
                    cursor: pointer;
                    width: max-content;
                    scale: 1.15;
                }
            }
            
            .service {
                width: 300px;
                height: 300px;
                position: absolute;
                z-index: 100;
                scale: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                gap: 10px;
                cursor: pointer;
                &:hover {
                    img {
                        scale: 1.05!important; 
                    }
                }
                

                i {
                    font-size: 45px;
                    z-index: 2;
                }
                p {
                    padding: 0;
                    margin: 0;
                }
                p.head {
                    font-size: 21px;
                    line-height: 32px;
                    font-weight: 400;
                    letter-spacing: 0;
                    color: #E7E7E7;
                    font-family: "DM Sans", sans-serif;
                    text-align: center;
                    z-index: 2;
                    min-width: 300px;
                    margin-top: 5px;
                }
                p.description {
                    font-size: 15px;
                    line-height: 32px;
                    font-weight: 400;
                    letter-spacing: 0;
                    color: #E7E7E7;
                    font-family: "DM Sans", sans-serif;
                    text-align: center;
                    z-index: 2;
                    min-width: 300px;
                }
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    position: absolute;
                    transition: scale 3s; 

                
                }
            }
            .service:nth-child(1) {
                left: 10%;
                top: 15%;
            }
            .service:nth-child(2) {
                right: 8%;
                top: 9%;
            }
            .service:nth-child(3) {
               left: 24%;
               bottom: 9%;
            }
            .service:nth-child(4) {
                right: 20%;
                bottom: 9%;
            }
             .service:nth-child(5) {
                right: 44%;
                bottom: 28%;
                width: 80px;
                height: 80px;
            }
            .service:nth-child(6) {
                left: 7%;
                bottom: 26%;
                width: 150px;
                height: 150px;
            }
            .service:nth-child(7) {
                right: 4%;
                bottom: 36%;
                width: 150px;
                height: 150px;
            }
        }

        .mouse {
            width: 30px;
            height: 50px;
            position: absolute;
            bottom: 15px;
            left: calc(50% - 15px);
            border: 2px solid white;
            border-radius: 50px;

            .wheel {
                width: 5px;
                height: 10px;
                background: white;
                position: absolute;
                top: 8px;
                left: calc(50% - 2px);
                border-radius: 9px;
                animation: mouse 0.5s linear infinite;
                animation-direction: alternate;
            }
        }

    }
}

@keyframes moveLine {
    0% {
        left: 0;
    }
    100% {
        left: 100%;
    }
}

@keyframes text-bg-animate {
    0% {
        background-position: 0 0;
    }
    50% {
        background-position: 100% 0;
    }
    100% {
        background-position: 0 0;
    }
}

@keyframes mouse {
    0% {
        top: 8px;
    }
    100% {
        top: 14px;
    }
}